.wrapper{
    display: flex;
    justify-content: center;
}

.jobs {
    width: 80vw;
}

.title {
    color: #ff9a6b;
    font-size: 2.5vw;
    padding: 10px;
}

.jobs p{
    padding: 10px;
    font-size: 20px;
}
.jobs p:nth-child(3){
    margin-left: 15px;
    padding: 0 0 30px 0;
}

@media screen and (max-width: 768px) {
    .jobs p{
        font-size: 15px;
    }
    .title{
        font-size: 20px;
    }
    .jobs p:nth-child(3){
        margin-left: 30px;
    }
}