.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.baseTechnologiesBlock {
    display: flex;
    flex-direction: column;
    width: 80vw;
}

.title {
    color: #ff9a6b;
    font-size: 30px;
    padding: 10px;
    margin-top: 10px;
}

.btSkills {
    display: flex;
    justify-content: center;
}

.btSkills div{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
    font-size: 20px;
}

.expBLock{
    width: 80vw;
}

.expBLock p{
    font-size:20px;
    padding: 10px;
}

.coursesBlock{
    width: 80vw;
}

.coursesBlock p{
    font-size: 20px;
    padding: 3px 0 3px 10px;
    display: flex;
    align-items: center;
}

.coursesBlock img{
    height: 10px;
}

.coursesBlock span{
    margin-left: 10px;
}


.study {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 80vw;
}

.study div{
    width: 50%;
}

.study h4{
    font-size: 20px;
    color: #ff9a6b;
    padding: 10px;
}

.study p{
    font-size: 15px;
    padding: 10px;
}

@media screen and (max-width: 768px) {
    .title{
        font-size: 20px;
    }
    .expBLock p{
        font-size:15px;
    }
    .coursesBlock p{
        font-size: 15px;
    }
    .study h4{
        font-size: 15px;
    }
    .study p{
        font-size: 10px;
    }
}
