.Wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.AboutMeHeader {
    width: 80vw;
    display: flex;
    flex-direction: column;
    margin: 15px;
}

.AboutMeHeader h1{
    font-size: 5vw;
}

.AboutMeHeader div:nth-child(2){
    display: flex;
    justify-content: flex-end;
}

.AboutMeHeader span{
    color: #ff9a6b;
}

.AboutMeInfo {
    width: 80vw;
    display: flex;
}

.AboutMeInfo > div{
    width: 50%;
    display: flex;
    justify-content: center;
}

.AboutMeInfo div:nth-child(2){
    display: flex;
    flex-direction: column;
    align-items: center;
}

.AboutMeInfo div:nth-child(1){
    display: flex;
    justify-content: center;
    align-items: center;
}

.AboutMeInfo img{
    height: 270px;
}

.AboutMeInfo h2{
    font-size: 4vh;
    color: #ff9a6b;
}

.AboutMeBlock {
    display: flex;
    flex-direction: column;
    align-items: center;
    line-height: 1.5;
}

.AboutMeBlock p{
    font-size: 1.5vw;
}

.link {
    font-size: 1.5vw;
    margin: 15px 0;
}

@media screen and (max-width: 768px) {
    .AboutMeInfo img{
        height: 150px;
    }
}